/* Sidebar.css */
.sidebar {
    height: 100vh;
    width: 250px;
    background-color: #2c3e50;
    color: #ecf0f1;
    transition: width 0.3s ease;
    overflow: hidden;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  
  .sidebar.collapsed {
    width: 70px;
  }
  
  .toggle-btn {
    background: none;
    border: none;
    color: #ecf0f1;
    font-size: 20px;
    cursor: pointer;
    margin: 10px;
  }
  
  .sidebar-nav ul {
    list-style-type: none;
    padding: 0;
  }
  
  .sidebar-nav li {
    width: 100%;
    display: flex;
    align-items: center;
  }
  
  .sidebar-nav a {
    display: flex;
    align-items: center;
    padding: 15px 20px;
    color: #ecf0f1;
    text-decoration: none;
    width: 100%;
    transition: background 0.2s ease;
  }
  
  .sidebar-nav a:hover {
    background-color: #34495e;
  }
  
  .icon {
    font-size: 24px;
    margin-right: 15px;
  }
  
  .link-text {
    font-size: 18px;
  }
  